import { useState, useEffect } from 'react';
import { ethers, BigNumber } from 'ethers';
import { Box, Button, Flex, Input, Text} from "@chakra-ui/react";

import TestNFT from './TestNFT.json';

const TestNFTAddress = "0x726BDfF23bd933b81A8E2962C7176F50C6A8a135";

const MainMint = ({ accounts, setAccounts }) => {
    const [mintAmount, setMintAmount] = useState(1);
    const isConnected = Boolean(accounts[0]);

    async function handleMint() {
        if (window.ethereum) {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            const contract = new ethers.Contract(
                TestNFTAddress,
                TestNFT.abi, 
                signer
            );
            try {
                const response = await contract.mint(BigNumber.from(mintAmount), {
                    value: ethers.utils.parseEther((mintAmount * 0.01).toString())
                });
                console.log("response: ", response);
            } catch (err) {
                console.error("error", err);
            }
        }
    }

    const handleDecrement = () => {
        if (mintAmount <= 1) return;
        setMintAmount(mintAmount - 1);
    }

    const handleIncrement = () => {
        if (mintAmount >= 5) return;
        setMintAmount(mintAmount + 1);
    }

    const [counter, setCounter] = useState(0);
    const [maxSupply, setMaxSupply] = useState(10000);

    const getSupplyValues = async () => {
        if (window.ethereum) {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            const contract = new ethers.Contract(
                TestNFTAddress,
                TestNFT.abi,
                signer
            );
            const totalSupply = await contract.totalSupply();
            const max = await contract.maxSupply();
            setCounter(totalSupply.toNumber());
            setMaxSupply(max.toNumber());
        }
    }

    useEffect(() => {
        getSupplyValues();
    }, []);

    return (
        <Flex justify="center" align="center" height="100vh" paddingBottom="150px">
            <Box width="520px">
                <div>
                    <Text fontSize="48px" textShadow="0 5px #000000">TestNFT</Text>
                    <Text 
                        fontSize="30px"
                        letterSpacing="-5.5%"
                        fontFamily="VT323"
                        textShadow="0 2px 2px #000000"
                    >
                        lorem ipsum
                    </Text>
                </div>
                {isConnected ? (
                    <>
                        <div>
                            <Text fontSize="30px">
                                {counter}/{maxSupply}
                            </Text>
                        </div>
                        <div>
                            <Flex align="center" justify="center">
                                <Button
                                    backgroundColor="#D6517D"
                                    borderRadius="5px"
                                    boxShadow="0px 2px 2px 1px #0F0F0F"
                                    color="white"
                                    cursor="pointer"
                                    fontFamily="inherit"
                                    padding="15px"
                                    marginTop="15px"
                                    onClick={handleDecrement}
                                >
                                -
                                </Button>
                                <Input
                                    readOnly
                                    fontFamily="inherit"
                                    width="100px"
                                    height="40px"
                                    textAlign="center"
                                    paddingLeft="19px"
                                    marginTop="10px"
                                    type='number'
                                    value={mintAmount}
                                />
                                <Button
                                    backgroundColor="#D6517D"
                                    borderRadius="5px"
                                    boxShadow="0px 2px 2px 1px #0F0F0F"
                                    color="white"
                                    cursor="pointer"
                                    fontFamily="inherit"
                                    padding="15px"
                                    marginTop="15px"
                                    onClick={handleIncrement}
                                >
                                +
                                </Button>
                            </Flex>
                            <Button
                                backgroundColor="#D6517D"
                                borderRadius="5px"
                                boxShadow="0px 2px 2px 1px #0F0F0F"
                                color="white"
                                cursor="pointer"
                                fontFamily="inherit"
                                padding="15px"
                                marginTop="10px"
                                onClick={handleMint}
                            >
                            MINT NOW
                            </Button>
                        </div>
                    </>
                ) : (
                    <p>You must be connected to Mint.</p>
                )}
            </Box>
        </Flex>
        );
}

export default MainMint;